@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:"Raleway",sans-serif;color:#fff}body{background-color:#222}.container{padding-top:40px;padding-right:15px;padding-left:15px;margin-right:auto;margin-left:auto}nav{display:flex;justify-content:center;align-items:center;background-color:#000;height:60px}nav a{text-decoration:none;transition:.3s}nav a:hover{opacity:.8;transition:.3s}nav img{height:50px}.hero{position:relative;display:flex;flex-direction:column;justify-content:center;align-items:center;width:80%;height:50vh;min-height:400px;padding:0 10%;text-align:center;background-image:url(/gif-finder/static/media/rainbow_gradient.fa66324b.gif);background-color:rgba(0,0,0,.2);background-blend-mode:darken;background-repeat:no-repeat;background-size:cover}.hero h1{font-size:2.6rem}.hero .btn{text-decoration:none;background-color:#fff;color:#000;font-weight:bold;padding:15px 30px;border-radius:25px}.hero p{position:absolute;bottom:0}.grid .gif{margin:5px;width:calc(25% - 10px);height:180px;object-fit:cover;border-radius:5px}@media(max-width: 576px){.container{width:100%}}@media(min-width: 576px){.container{width:540px}}@media(min-width: 768px){.container{width:720px}}@media(min-width: 992px){.container{width:960px}}@media(min-width: 1200px){.container{width:1140px}}@media(min-width: 1400px){.container{width:1320px}}
